.button {
  position: relative;
  display: block;
  background: transparent;
  line-height: 60px;
  text-decoration: none;
}

.HexagonText {
  text-align: center;
}

.gray {
  color: var(--Black-100);
}

.gray:before,
.gray:after {
  border: 2px solid;
  border-color: var(--Black-40);
}

.button:before {
  position: absolute;
  content: "";
  border-bottom: none;
  -webkit-transform: perspective(15px) rotateX(5deg);
  -moz-transform: perspective(15px) rotateX(5deg);
  transform: perspective(15px) rotateX(5deg);
}
.button:after {
  position: absolute;
  top: 31px;
  content: "";
  border-top: none;
  -webkit-transform: perspective(15px) rotateX(-5deg);
  -moz-transform: perspective(15px) rotateX(-5deg);
  transform: perspective(15px) rotateX(-5deg);
}

